module.exports = "<h1><a id=\"VueLayers_0\"></a>VueLayers</h1>\n<blockquote>\n  <p>Vue components to work with <a href=\"https://openlayers.org\">OpenLayers</a></p>\n</blockquote>\n<p><a href=\"https://travis-ci.org/ghettovoice/vuelayers\"><img src=\"https://travis-ci.org/ghettovoice/vuelayers.svg?branch=master\" alt=\"Build Status\"></a>\n  <a href=\"https://coveralls.io/github/ghettovoice/vuelayers?branch=master\"><img src=\"https://coveralls.io/repos/github/ghettovoice/vuelayers/badge.svg?branch=master\" alt=\"Coverage Status\"></a>\n  <a href=\"http://standardjs.com\"><img src=\"https://img.shields.io/badge/code%20style-standard-brightgreen.svg\" alt=\"js-standard-style\"></a>\n  <a href=\"https://github.com/ghettovoice/vuelayers/releases\"><img src=\"https://img.shields.io/github/tag/ghettovoice/vuelayers.svg\" alt=\"GitHub tag\"></a>\n  <a href=\"https://www.npmjs.com/package/vuelayers\"><img src=\"https://img.shields.io/npm/v/vuelayers.svg\" alt=\"NPM version\"></a>\n  <a href=\"https://github.com/ghettovoice/vuelayers/blob/master/LICENSE\"><img src=\"https://img.shields.io/github/license/ghettovoice/vuelayers.svg\" alt=\"License\"></a>\n  <a href=\"https://david-dm.org/ghettovoice/vuelayers\"><img src=\"https://img.shields.io/david/ghettovoice/vuelayers.svg\" alt=\"Dependencies\"></a>\n  <a href=\"https://david-dm.org/ghettovoice/vuelayers?type=dev\"><img src=\"https://img.shields.io/david/dev/ghettovoice/vuelayers.svg\" alt=\"Dev dependencies\"></a></p>\n<p><strong><a href=\"https://ghettovoice.github.io/vuelayers/\">Demo</a></strong></p>\n<h2><a id=\"Install_15\"></a>Install</h2>\n<pre><code class=\"language-bash\"><span class=\"hljs-comment\"># install Vue and VueLayers</span>\nnpm install -S vue vuelayers\n</code></pre>\n<h2><a id=\"Available_builds_22\"></a>Available builds</h2>\n<ol>\n  <li><strong>UMD</strong></li>\n</ol>\n<ul>\n  <li>Full debug version: <code>dist/vuelayers.umd.js</code> and <code>dist/vuelayers.umd.css</code></li>\n  <li>Full production version: <code>dist/vuelayers.umd.min.js</code> and <code>dist/vuelayers.umd.min.css</code></li>\n</ul>\n<ol start=\"2\">\n  <li><strong>CommonJS</strong></li>\n</ol>\n<ul>\n  <li>Full version: <code>dist/vuelayers.cjs.js</code> and <code>dist/vuelayers.cjs.css</code></li>\n</ul>\n<ol start=\"3\">\n  <li><strong>CommonJS</strong> separate components (for plugins like <code>babel-plugin-component</code>)</li>\n</ol>\n<ul>\n  <li>Main: <code>dist/modules/index.js</code> and <code>dist/modules/style.css</code></li>\n  <li>Components: <code>dist/modules/%component-name%/index.js</code> and <code>dist/modules/%component-name%/style.css</code></li>\n</ul>\n<ol start=\"4\">\n  <li><strong>ES6 module</strong></li>\n</ol>\n<ul>\n  <li>Full version: <code>dist/vuelayers.es.js</code> and <code>dist/vuelayers.es.css</code></li>\n</ul>\n<h2><a id=\"Usage_35\"></a>Usage</h2>\n<h3><a id=\"Basic_example_with_full_import_37\"></a>Basic example with full import</h3>\n<pre><code class=\"language-js\"><span class=\"hljs-comment\">// main.js</span>\n<span class=\"hljs-keyword\">import</span> Vue <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'vue'</span>\n<span class=\"hljs-keyword\">import</span> VueLayers <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'vuelayers'</span>\n\nVue.use(VueLayers)\n// now all components installed and ready to use\nnew Vue({\n  el: <span class=\"hljs-string\">'#app'</span>,\n  render: h =&gt; h(App)\n})\n</code></pre>\n<pre><code class=\"language-html\">// App.vue\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-title\">template</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-title\">div</span> <span class=\"hljs-attribute\">id</span>=<span class=\"hljs-value\">\"map\"</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-title\">vl-map</span>&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-title\">vl-view</span> /&gt;</span>\n\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-title\">vl-layer-tile</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-title\">vl-source-osm</span> /&gt;</span>\n      <span class=\"hljs-tag\">&lt;/<span class=\"hljs-title\">vl-layer-tile</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-title\">vl-map</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-title\">div</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-title\">template</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-title\">script</span>&gt;</span><span class=\"javascript\">\n  <span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">default</span> {}\n</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-title\">script</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-title\">style</span>&gt;</span><span class=\"css\">\n  <span class=\"hljs-comment\">/* CSS file needs to be imported separately. */</span>\n  <span class=\"hljs-at_rule\">@<span class=\"hljs-keyword\">import</span> <span class=\"hljs-string\">\"~vuelayers/dist/vuelayers.\"</span></span>;\n</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-title\">style</span>&gt;</span>\n</code></pre>\n<p><strong>Note about usage of different builds</strong></p>\n<ul>\n  <li>\n    <p>For browser is available pre-build UMD version by simply including <code>dist/vuelayers.umd.min.js</code> and <code>vuelayers.umd.min.css</code> files<br>\n      on the page after VueJS.<br>\n      Or use from CDN like <a href=\"https://unpkg.com\">unpkg.org</a>.</p>\n    <ul>\n      <li><a href=\"https://unpkg.com/vuelayers@latest/dist/vuelayers.umd.min.js\">https://unpkg.com/vuelayers@latest/dist/vuelayers.umd.min.js</a></li>\n      <li><a href=\"https://unpkg.com/vuelayers@latest/dist/vuelayers.umd.min.css\">https://unpkg.com/vuelayers@latest/dist/vuelayers.umd.min.css</a></li>\n    </ul>\n  </li>\n  <li>\n    <p>For NodeJS is available CommonJS version (it is included by default) from <code>dist/vuelayers.cjs.js</code> and<br>\n      appropriate stylesheet <code>dist/vuelayers.cjs.css</code>.<br>\n      See below for example of incremental loading of what you need with tools like <code>babel-plugin-component</code>.</p>\n  </li>\n  <li>\n    <p>For bundlers like Webpack 2 and Rollup is available ES6 module version from <code>dist/vuelayers.es.js</code> and<br>\n      appropriate stylesheet <code>dist/vuelayers.es.css</code>.</p>\n  </li>\n</ul>\n<h3><a id=\"Incremental_import_89\"></a>Incremental import</h3>\n<p>With Webpack 2 or Rollup may be used without additional configuration, simply import what you need.<br>\n  Stylesheet should be imported manually (<code>dist/vuelayers.es.css</code>).</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> Vue <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'vue'</span>\n<span class=\"hljs-keyword\">import</span> { Map, View, TileLayer, OsmSource } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'vuelayers'</span>\n\nVue.use(Map)\nVue.use(View)\nVue.use(TileLayer)\nVue.use(OsmSource)\n\nnew Vue({\n  el: <span class=\"hljs-string\">'#app'</span>,\n  render: h =&gt; h(App)\n})\n</code></pre>\n<p>For native CommonJS or bundlers that doesnt support ES6 module system you can use tools like <code>babel-plugin-component</code><br>\n  for incremental loading.<br>\n  First, install <a href=\"https://github.com/QingWei-Li/babel-plugin-component\">babel-plugin-component</a><br>\n  Example Babel config</p>\n<pre><code class=\"language-json\">{\n  \"<span class=\"hljs-attribute\">presets</span>\": <span class=\"hljs-value\">[\n    [<span class=\"hljs-string\">\"latest\"</span>, <span class=\"hljs-string\">\"stage-2\"</span>]\n  ]</span>,\n  \"<span class=\"hljs-attribute\">plugins</span>\": <span class=\"hljs-value\">[[<span class=\"hljs-string\">\"component\"</span>, [\n    {\n      \"<span class=\"hljs-attribute\">libraryName</span>\": <span class=\"hljs-value\"><span class=\"hljs-string\">\"vuelayers\"</span></span>,\n      \"<span class=\"hljs-attribute\">style</span>\": <span class=\"hljs-value\"><span class=\"hljs-literal\">true</span></span>,\n      \"<span class=\"hljs-attribute\">libDir</span>\": <span class=\"hljs-value\"><span class=\"hljs-string\">\"dist/modules\"</span>\n    </span>}\n  ]]]\n</span>}\n</code></pre>\n<p>Now you can import only what you need, only that components will be included in final build.</p>\n<h2><a id=\"Documentation_129\"></a>Documentation</h2>\n<p>TODO</p>\n<h2><a id=\"Build_Setup_133\"></a>Build Setup</h2>\n<pre><code class=\"language-bash\">git <span class=\"hljs-built_in\">clone</span> https://gitlab.com/ghettovoice/vuelayers.git\n<span class=\"hljs-built_in\">cd</span> vuelayers\n\n<span class=\"hljs-comment\"># install dependencies</span>\nnpm install\n\n<span class=\"hljs-comment\"># serve with hot reload at localhost:8080</span>\nnpm start\n\n<span class=\"hljs-comment\"># demo </span>\nnpm run demo\n\n<span class=\"hljs-comment\"># build</span>\nnpm run build\n\n<span class=\"hljs-comment\"># run unit tests</span>\nnpm run unit\n\n<span class=\"hljs-comment\"># run e2e tests</span>\nnpm run e2e\n\n<span class=\"hljs-comment\"># run all tests</span>\nnpm <span class=\"hljs-built_in\">test</span>\n</code></pre>\n<h2><a id=\"License_161\"></a>License</h2>\n<p><strong>MIT</strong>  2017, Vladimir Vershinin<br>\n  Based on <a href=\"https://vuejs.org/\">Vue</a> and <a href=\"https://openlayers.org/\">OpenLayers</a></p>\n"

